<template>
<div class="Main">
  <el-carousel indicator-position="none" :interval="3000" :autoplay="true" :loop="true">
    <el-carousel-item v-for="item in pictures" >
      <el-image :src="item.url"></el-image>
    </el-carousel-item>
  </el-carousel>
  <el-form :model="UserInfo" ref="UserInfo"
           class="form"
           label-position="left"
           :inline="true"
  >
    <el-form-item
        label="姓名"
        :rules="[
      { required: true, message: '姓名不能为空'},]"
        prop="name" class="formItem">
      <el-input v-model="UserInfo.name"></el-input>
    </el-form-item>
    <el-form-item
        label="学号"
        :rules="[
      { required: true,
      message: '请输入学号',
      },
      {
         type:'number',
         message: '学号必须输入数字'
      }
      ]"
        prop="id"
        class="formItem">
      <el-input  v-model.number="UserInfo.id" maxlength="10" show-word-limit></el-input>
    </el-form-item>
    <el-form-item class="buttonGroup">
      <el-button type="success" @click="submitForm('UserInfo')">签到</el-button>
      <el-button type="info" @click="resetForm('UserInfo')">重置</el-button>
    </el-form-item>
  </el-form>
</div>
</template>

<script>

export default {
  data() {
    return {
      count: 0,
      UserInfo: {
        name: '',
        id: '',
      },
      pictures:[
        {url:"https://i.loli.net/2021/10/15/LkYvehIsNUj537B.jpg"},
        {url:"https://i.loli.net/2021/10/15/Agkt5BzRSxPLyvK.jpg"},
        {url:"https://i.loli.net/2021/10/15/lyY28WNLO1vxzUq.jpg"}
      ],
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        console.log(this.UserInfo.id)
        if (valid)
        {
          this.$axios({
            method: 'post',//一定要改
            url: "/api/signIn",
            data: {
              name: this.UserInfo.name,
              id: this.UserInfo.id,
            }
          }).then(res => {
            let valid = res.data;
            if (valid === 'ok')
            {
              this.$message({
                message: '签到成功',
                type: 'success',
                duration: 1000,
              });
            }
            else if (valid==='error')
            {
              this.$message({
                message: '曾有过未签离',
                type: 'error',
                duration: 1000,
              });
            }
          })
          clearTimeout(this.timer);
          this.resetForm('UserInfo')
          this.timer=setTimeout(()=>{
            location.reload()
          },1500)

        }
        else
        {
          console.log('error submit!!');
          location.reload()
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style scoped>
.Main{
  }
.form{
  padding-top: 10%;
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
  }
.buttonGroup{
  padding-top: 5%;
  }
</style>