<template>
  <div class="Main">

    <el-carousel indicator-position="none" :interval="3000" :autoplay="true" :loop="true">
      <el-carousel-item v-for="item in pictures">
        <el-image :src="item.url"></el-image>
      </el-carousel-item>
    </el-carousel>
    <el-form :model="UserInfo" ref="UserInfo"
             class="form"
             label-position="left"
             :inline="true"
    >
      <el-form-item
          label="姓名"
          :rules="[
      { required: true,
      message: '请输入姓名',
      },
      ]"
          prop="name"
          class="formItem">
        <el-input v-model="UserInfo.name"></el-input>
      </el-form-item>
      <el-form-item class="buttonGroup">
        <el-button type="success" @click="logout('UserInfo')">签离</el-button>
        <el-button type="info" @click="resetForm('UserInfo')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

export default {
  data() {
    return {
      count: 0,
      UserInfo: {
        name: ''
      },
      pictures: [
        {url: "https://i.loli.net/2021/10/15/LkYvehIsNUj537B.jpg"},
        {url: "https://i.loli.net/2021/10/15/Agkt5BzRSxPLyvK.jpg"},
        {url: "https://i.loli.net/2021/10/15/lyY28WNLO1vxzUq.jpg"}
      ],
    }
  },
  methods: {
    logout(formName) {
      this.$refs[formName].validate((valid) => {
        console.log(this.UserInfo.name)
        if (valid)
        {
          this.$axios({
            method: 'get',//一定要改
            url: "/api/logout",
            params: {
              name: this.UserInfo.name,
            }
          }).then(res => {
            let valid = res.data;
            if (valid === 'ok')
            {
              this.$message({
                message: '签离成功',
                type: 'success',
                duration: 1000,
              });
            }
            else if (valid==='none')
            {
              this.$message({
                message: '未签到',
                type: 'error',
                duration: 1000,
              });
            }
            else if (valid==='error'){
              this.$message({
                message: '曾有过未签离',
                type: 'error',
                duration: 1000,
              });
            }
          })
          clearTimeout(this.timer);
          this.resetForm('UserInfo')
          this.timer=setTimeout(()=>{
            location.reload()
          },1500)
        }
        else
        {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style scoped>
.Main {
  }

.form {
  padding-top: 10%;
  display: flex;
  justify-items: center;
  align-items: center;
  flex-direction: column;
  }

.buttonGroup {
  padding-top: 5%;
  }
</style>