import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    username:'',
    id:'',
  },
  mutations: {
    //不能执行异步操作
    /*在这里处理数据，输入数据必须为state,后边是外界传入参数*/
    username(state,username){
      state.username=username;
    },
    id(state,id){
      state.id=id;
    },
  },
  //这里可以处理异步操作，但是还得调用mutation来触发
  actions: {
    addAsync(context,step){
      setTimeout(()=>{context.commit('add',step)},1000)
    }
  },
  getters:{
    addN(state){
      return "abababab"+state.count+""
    }
  },
  modules: {
  }
})
