<template>
  <div>
    <el-button class="button" @click="$router.push('/')">返回首页</el-button>
    <el-table
        :data="tableData"
        style="width: 100%;height: 90%"
        :default-sort="{prop: 'arriveTime', order: 'descending'}"
    >
      <el-table-column
          prop="name"
          align="center"
          label="姓名">
      </el-table-column>
      <el-table-column
          prop="arriveTime"
          align="center"
          label="签到时间">
      </el-table-column>
      <el-table-column
          label="操作"
          align="center"
          >
        <template slot-scope="scope">
          <el-popconfirm
              confirm-button-text='是的'
              cancel-button-text='我点错了'
              icon="el-icon-info"
              icon-color="red"
              title="确认签离吗？"
              @confirm="Detail(scope.$index, scope.row)"
          >
<!--            @click.native.prevent="Detail(scope.$index, scope.row)"-->
            <el-button slot="reference"
                       type="success"
                       size="small"
            >签离</el-button>
          </el-popconfirm>
<!--          <el-button-->
<!--              @click.native.prevent="Detail(scope.$index, scope.row)"-->
<!--              type="success"-->
<!--              size="small">-->
<!--            签离-->
<!--          </el-button>-->
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: "List",
  mounted() {
    this.$axios({
      method: 'get',
      url: '/api/studentList',
    }).then(res => {
      this.tableData = res.data
    })
  },
  methods: {
    Detail(index,row){
      console.log(index)
      console.log(row)
      this.$axios({
        method: 'get',
        url: '/api/logout',
        params:{
          id0:row.id0
        }
      }).then(res => {
          if(res.data==="ok"){
            this.$message({
              message: '签离成功',
              type: 'success',
              duration: 1000,
            });
            this.$router.go(0);
          }
      })
    }
  },
  data() {
    return {
      id0:"",
      tableData: [
      //     {
      //   "name": "1",
      //   "leaveTime": "3",
      //   "arriveTime": "2021-09-19 22:02:22"
      // },{
      //   "name": "2",
      //   "leaveTime": "3",
      //   "arriveTime": "2021-09-19 22:02:25"
      // },{
      //   "name": "3",
      //   "leaveTime": "3",
      //   "arriveTime": "2021-09-19 22:02:26"
      // },
      ]
    }
  }

}
</script>

<style scoped>
.button{
  width: 100%;
  background-color: cornflowerblue;
  color: #ffffff;
  }

</style>